import React, { useEffect, useState } from "react";
import { API_URL } from "../Catalog/Catalog";
import BasketCard from "../../components/BasketCard/BasketCard";
import "./Basket.css";
import { useSelector } from "react-redux";

const Basket = () => {
  const basketItems = useSelector(({ basket }) => basket.items);

  /**
   *        ,
   *      
   *    ,
   * . .      
   * (, , ,   1 )
   *    ,
   *        
   */
  const uniqueBasketItems = Object.keys(basketItems).length;
  /**
   *       ,
   *     basketItems
   */
  const totalAmount = Object.values(basketItems).reduce(
    (total, amount) => (total += amount),
    0
  );

  const [products, setProducts] = useState([]);
  /**
   *      -,
   *     ,
   *   - products
   */
  const [totalPrice, setTotalPrice] = useState(0);

  /**
   *   
   * (     products)
   *   ,
   *     ,
   *   
   *   - basketItems
   */
  const updateProductsAmountValues = () => {
    const newProducts = products.map((product) => ({
      ...product,
      amount: basketItems[product.productId],
    }));

    setProducts(newProducts);
  };

  /**
   *    
   *    ,
   *   - products
   */
  const updateTotalPrice = () => {
    const newTotalPrice = products.reduce(
      (total, { amount, price }) => (total += amount * price),
      0
    );
    const newTotalPriceFormatted = newTotalPrice.toFixed(2);

    setTotalPrice(newTotalPriceFormatted);
  };

  const fetchProducts = () => {
    fetch(API_URL)
      .then((response) => response.json())
      .then((responseData) => {
        const allProducts = responseData.flowerlist;
        const newProducts = [];

        allProducts.forEach((product) => {
          /**
           *    'product.productId',
           *    'id'
           */
          const id = product.productId;
          /**
           *      
           *      'basketItems'
           *  'hasOwnProperty',  true  false,
           *       
           *      
           */
          const isExistInBasket = basketItems.hasOwnProperty(id);

          if (isExistInBasket) {
            newProducts.push({
              ...product,
              amount: basketItems[id],
            });
          }
        });

        setProducts(newProducts);
      });
  };

  /**
   *      ,
   *       
   */
  useEffect(() => {
    fetchProducts();
  }, [uniqueBasketItems]);

  /**
   *   'amount'
   *    - 'products'
   *      
   * - basketItems
   */
  useEffect(() => {
    updateProductsAmountValues();
  }, [basketItems]);

  /**
   *   
   *    ,
   *   - products
   */
  useEffect(() => {
    updateTotalPrice();
  }, [products]);

  return (
    <div className="basket">
      <div className="basket__body">
        <h1 className="basket__title">Your Cart</h1>
        {products.length ? (
          <ul className="basket__list">
            {products.map((product) => {
              const { category, name, price, productId, amount } = product;

              return (
                <li className="basket__item" key={productId}>
                  <BasketCard
                    title={name}
                    category={category}
                    price={price}
                    id={productId}
                    amount={amount}
                    imgSrc="https://placekitten.com/146/146"
                  />
                </li>
              );
            })}
          </ul>
        ) : (
          <div className="basket__empty-message">Basket is empty</div>
        )}
      </div>
      <div className="basket__summary">
        <div className="basket__summary-info">
          Subtotal for {totalAmount} items: {totalPrice}$
        </div>
        <button className="basket__summary-submit-button" type="button">
          Checkout
        </button>
      </div>
    </div>
  );
};

export default Basket;
