import { createSlice } from "@reduxjs/toolkit";

export const basketSlice = createSlice({
  name: "basket",
  initialState: {
    items: {},
  },
  reducers: {
    addToBasket: (state, action) => {
      const id = action.payload;
      const newBasketItems = { ...state.items };
      /**
       *     'basketItems'
       *    'id'    'hasOwnProperty',
       *  true,        
       */
      const isAlreadyExistInBasket = newBasketItems.hasOwnProperty(id);

      /**
       *       ,
       *     +1,
       *      1
       */
      isAlreadyExistInBasket
        ? (newBasketItems[id] += 1)
        : (newBasketItems[id] = 1);

      state.items = newBasketItems;
    },
    removeFromBasket: (state, action) => {
      const id = action.payload;
      const newBasketItems = { ...state.items };

      /**
       *     basketItems    'id'
       */
      delete newBasketItems[id];

      state.items = newBasketItems;
    },
    increaseBasketItem: (state, action) => {
      const id = action.payload;
      const newBasketItems = { ...state.items };

      newBasketItems[id]++;

      state.items = newBasketItems;
    },
    decreaseBasketItem: (state, action) => {
      const id = action.payload;
      const newBasketItems = { ...state.items };

      const currentAmount = newBasketItems[id];
      if (currentAmount > 1) {
        newBasketItems[id]--;
      } else {
        delete newBasketItems[id];
      }

      state.items = newBasketItems;
    },
  },
});

//    (   basketSlice).
//     ,   ,   ,
//          
export const {
  addToBasket,
  removeFromBasket,
  increaseBasketItem,
  decreaseBasketItem,
} = basketSlice.actions;

//    
export default basketSlice.reducer;
